import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
      recoms: [],
      posters:[],
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({value: event.target.value});
  }

  handleSubmit(e) {
    var data = require('./movie.json');
    var recos = data[this.state.value][0]['recos'];
    console.log(this.state.value);
    console.log(data[this.state.value][0]);
    console.log(recos);
    this.setState({recoms: recos});  
    console.log(this.state.recoms);

    console.log(recos);
    var arr = [];
    recos.map((reco) => {
        var url = 'http://www.omdbapi.com/?t=' + reco +'&apikey=2afc6891';
        fetch(url)
        .then(response =>response.json())
        .then(data => {
          console.log(data);
          console.log(data['Poster']);
          arr.push(data['Poster']);
          console.log(arr);
        });
    });

    console.log(arr);
    this.setState({posters:arr});
    console.log(this.state.posters);
    e.preventDefault();  
  }

  renderList(){
    console.log("RenderList")
    var posters = this.state.posters;

    posters.map((poster) => {
      return(
        <div class="gallery">
            <a target="_blank" href={poster}>
            <img src= {poster} alt="" width="600" height="400"></img>
            </a>
        </div>
      );
    })
  }

  render() {
    return (
      <div>      
        <form onSubmit={this.handleSubmit}>
        <label>
          Name:
          <input type="text" value={this.state.value} onChange={this.handleChange} />
        </label>
        <input type="submit" value="Submit" onClick = {this.handleSubmit.bind(this)}/>
      </form>
     {this.renderList()}
</div>
    );
  }
}

export default App;
